.button {
  width: 150px;
  height: 30px;
  color: #fff;
  border-color: #3498db;
  border-radius: 8px;
  font-size: 15px;
  transition: all .15s ease-in-out;
  box-shadow: inset 0 0 40px 40px #3498db, 0 0 #3498db;
}

.button:hover {
  color: #000;
  box-shadow: inset 0 0 10px #3498db, 0 0 10px 4px #3498db;
}

.button:disabled {
  box-shadow: none;
  color: #000;
  cursor: pointer;
}

.input {
  width: 200px;
  background: #f6f6f6;
  border: 1px solid #cecece;
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 6px 0 4px 10px;
  font-size: 15px;
}

.form {
  align-items: left;
  flex-direction: column;
  display: flex;
}

